import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useState, useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Typography } from '@mui/material';

// routes
import { PATH_ADMIN } from '../../routes/paths';
import { FormProvider, RHFSelect, RHFSwitch, RHFTextField } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

CreateDataPlan.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
export default function CreateDataPlan({ isEdit, currentUser }) {
  const { enqueueSnackbar } = useSnackbar();
  const [Habukhannetwork, setNetwork] = useState();
  const allplantype = [
    'GIFTING',
    'COOPERATE GIFTING',
    'SME',
    'SME 2',
    'DATASHARE'
  ];
  const allplansize = [
    'MB',
    'GB',
    'TB'
  ];
  const NewUserSchema = Yup.object().shape({
    plan_name: Yup.string().required('Plan Name Required'),
    plan_size: Yup.string().required('Plan Size Required'),
    network: Yup.string().required('Network Required'),
    plan_type: Yup.string().required('Plan Type Required'),
    smart: Yup.string().required('Smart Price Required'),
    awuf: Yup.string().required('Awuf Price Required'),
    agent: Yup.string().required('Agent Price Required'),
    api: Yup.string().required('Api Price Required'),
    special: Yup.string().required('Special Price Required'),
    plan_day: Yup.string().required(),
  });
  const defaultValues = useMemo(
    () => ({
      network: currentUser?.network || '',
      plan_name: currentUser?.plan_name || '',
      plan_size: currentUser?.plan_size || '',
      plan_type: currentUser?.plan_type || '',
      smart: currentUser?.smart || '',
      awuf: currentUser?.awuf || '',
      agent: currentUser?.agent || '',
      special: currentUser?.special || '',
      api: currentUser?.api || '',
      plan_day: currentUser?.plan_day || '',
      plan_status: currentUser?.plan_status || false,
      plan_id: currentUser?.plan_id || '',
      habukhan1: currentUser?.habukhan1 || '',
      habukhan2: currentUser?.habukhan2 || '',
      habukhan3: currentUser?.habukhan3 || '',
      habukhan4: currentUser?.habukhan4 || '',
      habukhan5: currentUser?.habukhan5 || '',
      msorg1: currentUser?.msorg1 || '',
      msorg2: currentUser?.msorg2 || '',
      msorg3: currentUser?.msorg3 || '',
      msorg4: currentUser?.msorg4 || '',
      msorg5: currentUser?.msorg5 || '',
      virus1: currentUser?.virus1 || '',
      virus2: currentUser?.virus2 || '',
      virus3: currentUser?.virus3 || '',
      virus4: currentUser?.virus4 || '',
      virus5: currentUser?.virus5 || '',
      simserver: currentUser?.simserver || '',
      simhosting: currentUser?.simhosting || '',
      smeplug: currentUser?.smeplug || '',
      msplug: currentUser?.msplug || '',
      ogdamns: currentUser?.ogdamns || '',
      easyaccess: currentUser?.easyaccess || '',
      free1: currentUser?.free1 || '',
      free2: currentUser?.free2 || '',
      free3: currentUser?.free3 || '',
      free4: currentUser?.free4 || '',
      free5: currentUser?.free5 || '',
      megasub: currentUser?.megasub || '',
      megasubcloud: currentUser?.megasubcloud || '',

      adex1: currentUser?.adex1 || '',
      adex2: currentUser?.adex2 || '',
      adex3: currentUser?.adex3 || '',
      adex4: currentUser?.adex4 || '',
      adex5: currentUser?.adex5 || '',
      boltnet: currentUser?.boltnet || '',
      zimrax: currentUser?.zimrax || '',
      hamdala: currentUser?.hamdala || '',
      autopilot: currentUser?.autopilot || '',

    }),
    [currentUser]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    axios.get('/api/website/app/network').then(data => {
      setNetwork(data.data?.network);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);
  const values = watch();
  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/add/data/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      enqueueSnackbar('Create success!');
      navigate(PATH_ADMIN.plan.data);
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }
  const onCreateReset = async (data) => {
    try {
      await axios.post(`/api/add/data/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      reset();
      enqueueSnackbar('Create success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  const onEdit = async (data) => {
    try {
      await axios.post(`/api/system/admin/edit/dataplan/dataplan/habukhan/secure/boss/asd/asd/changes/${AccessToken}/secure`, data)
      navigate(PATH_ADMIN.plan.data);
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  const onEditReset = async (data) => {
    try {
      await axios.post(`/api/system/admin/edit/dataplan/dataplan/habukhan/secure/boss/asd/asd/changes/${AccessToken}/secure`, data)
      navigate(PATH_ADMIN.plan.newdata);
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(!isEdit ? onSubmit : onEdit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFSelect name="network" label="Network" placeholder="Network" defaultValue="MTN">
                <option value="" />
                {Habukhannetwork?.map((option) => (
                  <option key={option?.plan_id} value={option?.network}>
                    {option?.network}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="plan_type" label="Plan Type" defaultValue="GIFTING">
                <option value="" />
                {allplantype.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="plan_name" label="Plan Name" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFSelect name="plan_size" label="Plan Size" defaultValue="MB">
                <option value="" />
                {allplansize.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="plan_day" label="Month Validate" />
              <RHFTextField name="smart" label="Smart Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="awuf" label="Awuf Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="agent" label="Agent Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="api" label="Api Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="special" label="Special Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFSwitch
                name="plan_status"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      {values.plan_status ? 'ACTIVE' : 'DEACTIVATED'}
                    </Typography>
                  </>
                }

                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />

              <RHFTextField name="habukhan1" label="Habukhan 1 PLAN ID" />
              <RHFTextField name="habukhan2" label="Habukhan 2 PLAN ID" />
              <RHFTextField name="habukhan3" label="Habukhan 3 PLAN ID" />
              <RHFTextField name="habukhan4" label="Habukhan 4 PLAN ID" />
              <RHFTextField name="habukhan5" label="Habukhan 5 PLAN ID" />

              <RHFTextField name="msorg1" label="MSORG 1 PLAN ID" />
              <RHFTextField name="msorg2" label="MSORG 2 PLAN ID" />
              <RHFTextField name="msorg3" label="MSORG 3 PLAN ID" />
              <RHFTextField name="msorg4" label="MSORG 4 PLAN ID" />
              <RHFTextField name="msorg5" label="MSORG 5 PLAN ID" />

              <RHFTextField name="virus1" label="VIRUS 1 PLAN ID" />
              <RHFTextField name="virus2" label="VIRUS 2 PLAN ID" />
              <RHFTextField name="virus3" label="VIRUS 3 PLAN ID" />
              <RHFTextField name="virus4" label="VIRUS 4 PLAN ID" />
              <RHFTextField name="virus5" label="VIRUS 5 PLAN ID" />

              <RHFTextField name="free1" label="FREE 1 PLAN ID" />
              <RHFTextField name="free2" label="FREE 2 PLAN ID" />
              <RHFTextField name="free3" label="FREE 3 PLAN ID" />
              <RHFTextField name="free4" label="FREE 4 PLAN ID" />
              <RHFTextField name="free5" label="FREE 5 PLAN ID" />

              <RHFTextField name="easyaccess" label="EasyAccess PLAN ID" />

              <RHFTextField name="simserver" label="SIMSERVER PLAN ID" />
              <RHFTextField name="smeplug" label="SMEPLUG PLAN ID" />
              <RHFTextField name="msplug" label="MSPLUG PLAN ID" />
              <RHFTextField name="ogdamns" label="OGDAMNS PLAN ID" />
              <RHFTextField name="simhosting" label="SIMHOSTING PLAN ID" />

              <RHFTextField name="megasub" label="MEGA SUB PLAN ID" />
              <RHFTextField name="megasubcloud" label="101Tera Bytes PLAN ID" />

              <RHFTextField name="adex1" label="ADEX 1 PLAN ID" />
              <RHFTextField name="adex2" label="ADEX 2 PLAN ID" />
              <RHFTextField name="adex3" label="ADEX 3 PLAN ID" />
              <RHFTextField name="adex4" label="ADEX 4 PLAN ID" />
              <RHFTextField name="adex5" label="ADEX 5 PLAN ID" />
              <RHFTextField name="boltnet" label="BOLTNET PLAN ID" />
              <RHFTextField name="zimrax" label="ZIMRAX PLAN ID" />
              <RHFTextField name="hamdala" label="HAMDALA PLAN ID" />
              <RHFTextField name="autopilot" label="AUTOPILOT PLAN ID" />
            </Box>

            <Stack alignItems="flex-end" sx={{
              mt: 2, display: 'grid',
              columnGap: 1,
              rowGap: 2, gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
            }}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {!isEdit ? 'Create  then Return' : 'Save Changes then Return'}
              </LoadingButton>
              <LoadingButton type="button" onClick={handleSubmit(!isEdit ? onCreateReset : onEditReset)} variant="contained" color="warning" loading={isSubmitting}>
                {!isEdit ? ' Create then Reset' : 'Save Changes then Create New'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
